@import "https://fonts.googleapis.com/css?family=Open+Sans:100,300,400,500,900";
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", sans-serif; }

h1 {
